<template>
  <div>
    <v-app-bar
      fixed
      class="top"
      style="margin-top:env(safe-area-inset-top);"
      :elevation="tabBar==0?2:0"
      color="background"
      app
    >
      <v-btn
        icon
        color="primary"
        @click="navigationDialog=!navigationDialog;getCommentsNum();getSystemMessageNum()"
      >
        <v-icon large>mdi-account-circle</v-icon>
      </v-btn>
      <v-img contain max-width="72px" height="36px" :src="require('../../static/images/火锅底料2.png')"></v-img>
      <v-spacer></v-spacer>
    </v-app-bar>

    <v-app-bar
      fixed
      bottom
      elevation="6"
      color="background"
      :class="!$store.state.isUp?'animate__animated animate__slideInUp':'animate__animated animate__slideOutDown'"
      style=" --animate-duration: 200ms;"
    >
      <v-btn icon @click="change_tab_bar(0)" class="ml-1">
        <v-icon :size="tabBar==0?'33px':'28px'" :color="tabBar==0?'primary':'grey'">mdi-earth</v-icon>
      </v-btn>
      <v-btn icon @click="change_tab_bar(1)" class="ml-2">
        <v-icon :size="tabBar==1?'32px':'28px'" :color="tabBar==1?'primary':'grey'">mdi-firebase</v-icon>
      </v-btn>
    </v-app-bar>
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <v-navigation-drawer
      v-model="navigationDialog"
      absolute
      temporary
      dark
      style="overflow:visible;position:fixed;margin-top:env(safe-area-inset-top);"
    >
      <v-btn
        color="primary"
        fab
        absolute
        style="position:absolute"
        class="navigation_close"
        @click="navigationDialog=!navigationDialog"
      >
        <v-icon size="32px">mdi-close-thick</v-icon>
      </v-btn>
      <v-list-item>
        <v-list-item-avatar>
          <v-avatar size="32px" color="white" class="ml-n2">
            <v-icon color="grey darken-3" size="24px">mdi-account</v-icon>
          </v-avatar>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>火锅底料</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list>
        <v-list-item link single-line @click="toPersonalCenter('systemMessage')">
          <v-list-item-icon>
            <v-icon>mdi-cog-transfer</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title style="overflow:visible">
              <v-badge color="primary" dot v-if="newSystemMessage" class="mt-n3">系统消息</v-badge>
              <span v-else>系统消息</span>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item link single-line @click="toPersonalCenter('replyMe')">
          <v-list-item-icon>
            <v-icon>mdi-message-processing-outline</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title style="overflow:visible">
              <v-badge color="primary" dot v-if="newMessage" class="mt-n3">回复我的</v-badge>
              <span v-else>回复我的</span>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item link single-line @click="toPersonalCenter('historyPosts')">
          <v-list-item-icon>
            <v-icon>mdi-paperclip</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>历史发帖</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item link single-line @click="toPersonalCenter('historyComments')">
          <v-list-item-icon>
            <v-icon>mdi-send</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>历史评论</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item link single-line @click="toPersonalCenter('myCollect')">
          <v-list-item-icon>
            <v-icon>mdi-bookmark-multiple-outline</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>我的收藏</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item link single-line @click="toPersonalCenter('myConfig')">
          <v-list-item-icon>
            <v-icon>mdi-wrench</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>我的设置</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
      </v-list>
    </v-navigation-drawer>
    <v-dialog
      v-model="dialog"
      dark
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card height="180px" style="background-color:#363636">
        <v-card-title primary-title>
          <span style="font-size:64rpx">评教系统暂未开放</span>
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog=false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-spacer></v-spacer>
          <v-icon color="primary" size="80" class="float-right ml-n4">mdi-anchor</v-icon>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="announcementDialog.show"
      scrollable
      persistent
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card dark style="background-color:#363636">
        <v-card-title>
          <v-icon color="primary">mdi-bulletin-board</v-icon>
          <span class="ml-1">社区公告</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-2">
          <div v-html="announcementDialog.content"></div>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn outlined color="primary" @click="toHistoryAnnouncement">
            <span>历史公告</span>
          </v-btn>
          <v-btn color="primary" @click="knowAnnouncement">
            <span>我已知晓</span>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import store from "@/store/store.js";
var str = [store.state.tabBar];
export default {
  name: "layout",
  data() {
    return {
      navigationDialog: false,
      ...store.state,
      dialog: false,
      newMessage: false,
      newSystemMessage: false,
      announcementDialog: {
        show: false,
        content: ""
      },
      registerWithEmail: {
        show: false,
        email: "@stu.scu.edu.cn",
        filter: v => {
          var pattern = /^([A-Za-z0-9_\-\.\u4e00-\u9fa5])+@stu.scu.edu.cn/;
          return pattern.test(v) ? true : "邮箱不合法";
        },
        code: "",
        oneMinuteInterval: false,
        cnt: 20
      },
      announcementId: localStorage.getItem("announcementId")
    };
  },
  created() {
    console.log("新建组件", store.state.tabBar);
    this.tabBar = store.state.tabBar;
    let that = this;
    setTimeout(() => {
      this.$request("get", "/forum/getAnnouncement", {}, {}).then(res => {
        if (
          typeof res.data.id === "number" &&
          !isNaN(res.data.id) &&
          (!localStorage.getItem("announcementId") ||
            res.data.id > localStorage.getItem("announcementId"))
        ) {
          that.announcementDialog.show = true;
          that.announcementDialog.content = res.data.content;
          that.announcementId = res.data.id;
        }
      });
    }, 1000);
  },
  mounted() {},
  methods: {
    change_tab_bar(i) {
      if (i == 2) {
        this.dialog = true;
        return;
      }
      let that = this;
      let routerMap = ["index", "hot", "evaluation"];
      store.change_tab_bar(i);
      this.$nextTick(() => {
        console.log("changed");
        that.tabBar = store.state.tabBar;
      });
      this.$store.change_scrollYLeave();
      this.$router.replace(`/main/${routerMap[i]}`);
    },
    getCommentsNum() {
      let that = this;
      that
        .$request(
          "get",
          "/user/getCommentsNum",
          { timeToken: new Date().getTime() },
          {}
        )
        .then(res => {
          if (typeof res.data === "number" && !isNaN(res.data)) {
            sessionStorage.setItem("commentNumTemp", res.data);
            if (
              localStorage.getItem("commentNum") &&
              localStorage.getItem("commentNum") < res.data
            )
              that.newMessage = true;
            else if (!localStorage.getItem("commentNum") && res.data > 0)
              that.newMessage = true;
            else that.newMessage = false;
          } else {
            that.newMessage = false;
          }
        })
        .catch(e => {
          console.log(e);
          that.newMessage = false;
        });
    },
    knowAnnouncement() {
      this.announcementDialog.show = false;
      localStorage.setItem("announcementId", this.announcementId);
    },
    getSystemMessageNum() {
      let that = this;
      that
        .$request("get", "/user/getSystemMessageNum", {
          timeToken: new Date().getTime()
        })
        .then(res => {
          if (typeof res.data === "number" && !isNaN(res.data)) {
            sessionStorage.setItem("SystemMessageNumTemp", res.data);
            if (
              localStorage.getItem("SystemMessageNum") &&
              localStorage.getItem("SystemMessageNum") < res.data
            )
              that.newSystemMessage = true;
            else if (!localStorage.getItem("SystemMessageNum") && res.data > 0)
              that.newSystemMessage = true;
            else that.newSystemMessage = false;
          } else {
            that.newSystemMessage = false;
          }
        })
        .catch(e => {
          that.newSystemMessage = false;
        });
    },
    toPersonalCenter(path) {
      this.$store.change_scrollYLeave();
      this.$router.push(`/${path}`);
    },
    toHistoryAnnouncement() {
      window.open("http://scuhuoguodiliao.com/#/announcement", "_system", {});
    }
  },

  activated() {
    console.log("显示了");
    this.tabBar = store.state.tabBar;
  },
  watch: {
    dialog(v) {
      this.$store.change_dialog(v, "dialog");
    },
    "announcementDialog.show"(v) {
      this.$store.change_dialog(v, "announcementDialog.show");
    },
    "that.$store.state.drawer"(v) {
      if (this.$store.state.dialogName == "dialog") this.dialog = v;
      else if (this.$store.state.dialogName == "announcementDialog.show")
        this.announcementDialog.show = v;
    }
  }
};
</script>

<style>
.navigation_close {
  left: 85%;
  top: 30px;
  z-index: 99;
}

.navigation_background {
  bottom: -60px;
  left: 10px;
}
.animate__animated .animate__slideInUp {
  --animate-duration: 200ms;
}
.down {
  animation: down 200ms;
  animation-fill-mode: forwards;
}
.up {
  animation: up 200ms;
  animation-fill-mode: forwards;
}

@keyframes down {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(14px);
  }
  50% {
    transform: translateY(28px);
  }
  75% {
    transform: translateY(42px);
  }
  100% {
    transform: translateY(56px);
    display: none;
  }
}

@keyframes up {
  0% {
    transform: translateY(-0);
  }
  25% {
    transform: translateY(-14px);
  }
  50% {
    transform: translateY(-28px);
  }
  75% {
    transform: translateY(-42px);
  }
  100% {
    transform: translateY(-56px);
  }
}
</style>



